:root {
  --moderate-cyan: hsl(176, 50%, 47%);
  --dark-cyan: hsl(176, 72%, 28%);
  --black: hsl(0, 0%, 0%);
  --dark-gray: hsl(0, 0%, 48%);
  --gray: hsl(0, 0%, 90%);
  --light-gray: hsl(0, 0%, 95%);
}

:root {
  --font-commissioner: "Commissioner", sans-serif;
}

body {
  font-family: var(--font-commissioner);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-commissioner);
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

nav {
  height: 550px;
  background: url(../../../images/image-hero-desktop.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 50px;
}
nav .nav__container {
  display: flex;
  justify-content: space-between;
}
nav .nav__container ul {
  display: flex;
  list-style: none;
}
nav .nav__container ul li a {
  color: white;
  margin-left: 2rem;
}

.starter {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
}

.stats {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
}

.about {
  border: solid var(--gray) 1px;
  margin-bottom: 30px;
  border-radius: 10px;
  background: white;
}

body {
  background-color: var(--light-gray);
}

main {
  position: absolute;
  top: 425px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  max-width: 925px;
}